.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.counter {
  background-color: #272525;
  height: 100vh;
}

.counter-wrapper {
  border: 1px solid #61dafb;
  border-radius: 5px;
  width: 400px;

}

.counter-item {
  border: 1px solid #61dafb;
  border-radius: 5px;
  font-size: 100px;
  display: flex;
  justify-content: center;
  padding: 30px 0 30px 0;
  margin: 10px;
}


.button-wrapper {
  display: flex;
  justify-content: space-around;
  border: 1px solid #61dafb;
  margin: 10px;

}
button {
  border: 1px solid #61dafb;
  color: #61dafb;
  background-color: transparent;
  font-size: 30px;
  margin: 10px;
}
button:disabled{
  color: red;
  border:1px solid red;

}
.counter-span{
  font-size: 30px;
  font-weight: bold;
  color: #61dafb;
}
input{
    border: 2px solid #61dafb;
    margin-left: 20px;
}